import axios from 'axios';

export default function getUsers (){
abortController = new AbortController();
    return axios.get(`https://api.github.com/users`, { signal: abortController.signal })
    .then (data => {
        return data;
    })
    .catch(error => {
        if (error.name === 'AbortError') return; 
        throw error; 
      });
}